<template>
  <a-spin :spinning="loading">
    <a-form-model
      ref="form"
      :model="entity"
      :rules="rules"
      autocomplete="off"
      layout="vertical"
    >
      <div class="card">
        <a-row :gutter="24">
          <a-col :sm="6">
            <!--  -->
            <a-form-model-item
              label=""
              prop="workflowCode"
              ref="workflowCode"
            >
              <a-input
                v-model="entity.workflowCode"
                placeholder=""
                name="workflowCode"
                :disabled="!!ndoEditParams.row"
                @change="inChange"
              ></a-input>
            </a-form-model-item>
          </a-col>
          <a-col :sm="6">
            <!--  -->
            <a-form-model-item
              label=""
              prop="name"
              ref="name"
            >
              <a-input
                v-model="entity.name"
                placeholder=""
                name="name"
                @change="inChange"
              ></a-input>
            </a-form-model-item>
          </a-col>
          <a-col :sm="6">
            <!--  -->
            <a-form-model-item
              label=""
